@import url(https://spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);
/* ✅Reset */
body,
ul,
p {
  margin: 0px;
  padding: 0px;
}

li {
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

/* ✅Typography */

html {
  font-family: "SpoqaHanSans";
  font-size: 62.5%;
  line-height: 1.4;
}

body {
  font-size: 1.4rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: 100%;
  font-weight: normal;
}

/* ✅Commons */
.clearfix::before,
.clearfix::after {
  content: " ";
  display: table;
}

.clearfix::after {
  clear: both;
}

/* ✅Layouts*/
.l_wrapper {
  max-width: 980px;
  width: auto;
  padding: 0 30px;
  margin: 0 auto;
}

/* ✅Components */

body {
  background-color: #ffffff;
  color: #404040;
}

.header {
  color: black;
  padding: 20px 0;
  position: relative;
}

.header-logo {
  float: left;
  background-image: url(data:image/png;base64,);
  width: 68px;
  height: 40px;
  /*=> 🔸해더 로고는 a태그. 그래서 inline은 폭·넓이 지정 안됨.
  근데 float를 주면 display가 block가 됨!*/
  background-size: cover;
  /*=> 🔸로고img 경우 화면 깨짐 방지를 위해 원본보다 2배 크게 준비해
  css에서 절반 크기로 줄임=> 다운 샘플링*/
  text-indent: -9999px;
  /*=> text 안 보이게 처리*/
  display: block;
  /*=> 반응형 시 float:none 탓에 로고의 폭넓이 사라짐.
  display가 inline으로 돌아와서. 그래서 display에 block 추가.*/
}

.nav-h1 {
  display: none;
}

.nav-menu {
  float: right;
}

.nav-menu li {
  float: left;
  /*원래 자식 li가 flaot되면 부모 해더의 높이가 0이 됨.*/
  /*하지만 부모 해더메뉴도 이미 float라서 문제 안 생김*/
  /*border-left: 1px solid;*/ /*해더 color 색상*/
}

.nav-menu li a {
  display: block;
  height: 40px;
  /*로고 이미지 높이와 같게. 하지만 a태그는 inline.
    그래서 display를 block으로 설정*/
  line-height: 40px;
  /*한 줄 높이를 40px로 해주면
    텍스트 위아래로 공간이 생겨 세로 가운데 정렬 효과 발생*/
  padding: 0 10px;
}

.search {
  display: block;
  height: 40px;
  line-height: 40px;
  padding: 0 10px;
}

.search-input {
  text-align: center;
  background-color: #fafafa;
  color: #555;
  border: none;
  /* border-bottom: 1px solid #555; */
  line-height: center;
  margin: 0;
  padding: 0;
}

.search-input:hover {
  color: #000;
  border-bottom: 1px solid #333;
  -webkit-transition: border-bottom 0.3s;
  transition: border-bottom 0.3s;
}

/* ✅Mediaqueries */

/* Pc */
@media (min-width: 769px) {
  .l_header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    border-bottom: 1px solid #ddd;
    height: 45px;
    z-index: 900;
    background-color: rgba(247, 247, 247, 0.89);
  }
}

.feed {
  width: 100%;
  max-width: 520px;
  background-color: #fff;
  box-shadow: 0 2px 20px 0 rgba(0, 0, 0, 0.04);
  padding: 16px 20px;
  border-radius: 5px;
  border-bottom: 1px solid #ddd;
}

.feed button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #0257c7;
  cursor: pointer;
}

.feed button:hover,
.feed button:focus {
  outline: none;
  box-shadow: none;
}

.feed-user-profile {
  position: relative;
  display: flex;
  align-items: flex-end;
  width: 100%;
  margin-bottom: 16px;
}

.feed-user-profile > a {
  margin-right: 12px;
  line-height: 1;
}

.feed-user-profile > a img {
  width: 52px;
  height: 52px;
  border-radius: 40px;
}

.feed-user-profile div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.feed-user-profile h1 {
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  color: #212529;
}

.feed-user-profile a {
  color: inherit;
  text-decoration: none;
}

.feed-user-profile a:hover {
  opacity: 0.5;
}

.feed-user-profile span {
  color: #80868e;
  font-size: 12px;
  line-height: 16px;
}

.feed-user-profile button {
  position: absolute;
  top: 50%;
  right: 16px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 32px;
  padding: 0 16px;
  border: 1px solid #dbdee4;
  border-radius: 4px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: color 300ms, background-color 400ms, border-color 300ms;
  transition: color 300ms, background-color 400ms, border-color 300ms;
}

.feed-user-profile button.following {
  background-color: #006ffe;
  border-color: #006ffe;
  color: #fff;
}

.feed-content {
  margin-bottom: 8px;
}

.feed-content img {
  display: block;
  margin: 0px auto;
  width: 100%;
  height: 100%;
}

.feed-content p {
  font-size: 16px;
  line-height: 1.375;
  letter-spacing: -0.01em;
  color: #333e47;
  padding-top: 10px;
}

.feed-footer button {
  height: 32px;
  padding: 0;
  border: none;
  margin-right: 12px;
  color: #0081ff;
  background-color: #fff;
}

.feed-footer button:hover {
  color: #006ffe;
}

.feed-footer button:first-child.active {
  font-weight: 700;
}

.feed-comment {
  display: none;
  margin-top: 4px;
}

.feed-comment.active {
  display: block;
}

.feed-comment textarea {
  width: 100%;
  height: 64px;
  padding: 10px 12px;
  border: 1px solid #dbdee4;
  border-radius: 3px;
  margin-bottom: 4px;
  font-size: 14px;
  line-height: 1.4285714286;
  color: #333e47;
  -webkit-transition: border-color 250ms ease-in-out;
  transition: border-color 250ms ease-in-out;
}

.feed-comment textarea:focus {
  outline: none;
  border-color: #b6bec8;
}

.feed-comment textarea::-webkit-input-placeholder {
  color: #b6bec8;
}

.feed-comment textarea:-ms-input-placeholder {
  color: #b6bec8;
}

.feed-comment textarea::-ms-input-placeholder {
  color: #b6bec8;
}

.feed-comment textarea::placeholder {
  color: #b6bec8;
}

.feed-comment button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 32px;
  padding: 0 16px;
  border-radius: 4px;
  -webkit-transition: background-color 100ms;
  transition: background-color 100ms;
  background-color: #0081ff;
  border-color: #0081ff;
  color: #fff;
}

.feed-comment button:hover {
  background-color: #006ffe;
}

